/*               */
/* css variables */
/*               */
* {
    --cardZindex: 4;
    
    --transparentBackground: rgba(255, 255, 255, 0);
    --translucentBackground: rgba(211, 211, 211, .25);

    --boxShadowSettings: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

/* format card parent */
.cardContainer {
    /* coordinate */
    z-index: var(--cardZindex);
    position: absolute; /* super important */

    /* size */
    width: 100vw;
    height: 100vh;

    /* offset */
    padding-top: 64px;
    padding-left: 32px;
    padding-right: 32px;
    padding-bottom: 64px;

    /* text formatting */
    display: block;
    text-align: left;
    
    /* color/styling */
    background-image: linear-gradient(rgba(255, 255, 255, 0.75), rgba(255, 255, 255, 0.15));
    /* background-image: linear-gradient(rgba(0, 255, 179, 0.15), rgba(255, 255, 255, 0.15)); */
    -webkit-backdrop-filter: blur(3px);
            backdrop-filter: blur(3px);
}

/* format card children (contents) */
.textBlock {
    /* display controls whether each item
       item is on the same line or stacked */
    display: block;
    color: black;
}

.cardInnards {
    display: block;
}
.cardInnards input {
    float: right;
}

.headerContainer {
    position: relative;
    padding-bottom: 16px;

    line-height: 1px;
    border-bottom: 3px solid #000;
}
.labelContainer {
    position: static;
    padding-top: 8px;
    padding-bottom: 8px;
}
#logInFieldsContainer {
    position: relative;
    display: block;
    padding-top: 16px;
    padding-bottom: 8px;
}

#switchToSignUp {
    position: relative;
    padding-top: 8px;
    padding-bottom: 16px;
}

.labelContainer {
    display: block;
    padding-top: 8px;
    padding-bottom: 8px;
}
/* FIXING CSS ISSUES AFTER RESET */

/* super important for screen properties to work properly */
*{margin:0; padding:0;}
html, body {
    width: 100vw;
    height:100vh;
    font-family: "Roboto", sans-serif;
}

/* super important for padding to work properly */
* { box-sizing: border-box; }

h1,h2,h3,h4,h5,h6 {
    font-weight: normal;
    font-style: normal;
}

  button {
    font-size: 2rem;
    /* margin-left: 0.5rem; */
    /* border-radius: 0%; */
  }

/* this was supposed to be orientation lock, not quite that but does make things better */
@media screen and (min-width: 320px) and (max-width: 767px) and (orientation: landscape) {
html {
    transform: rotate(-90deg);
    transform-origin: left top;
    width: 100vh;
    overflow-x: hidden;
    position: absolute;
    top: 100%;
    left: 0;
}
}
