/*                        */
/* macro sidebar settings */
/*                        */

/* parent tag */
.sidebar {
    position: absolute;
    height: 100vh;
    width: 0;
    top: 0;
    left: 0;
    z-index: 7;
    
    /* color/styling */
    background-color: rgba(11, 3, 45, 0.95);
    -webkit-backdrop-filter: blur(3px);
            backdrop-filter: blur(3px);

    box-shadow: -9px 0px 18px black inset;

    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
}

.sidebar a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 15px;
    color: white;
    display: block;
    transition: 0.3s;
}
.sidebar a:hover {
    color: orange;
}

.sidebar .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 20px;
    margin-left: 50px;
}

#sidebarLogo {
    position: absolute;
    float: left;
    bottom: 0%;
    width: auto;
    color: white;
}

/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
    .sidebar {padding-top: 15px;}
    .sidebar a {font-size: 18px;}
}
/* container position must be absolute */
#refreshBtnContainer {
    /* container coordinate */
    z-index: 3;
    position: absolute;
    top: 10%;

    /* container size */
    width: 100vw;
    height: auto;

    /* container inside offset */
    padding: 16px;

    /* container text formatting */
    display: block;
    text-align: left;
}

