    /* button text formatting */
    text-align: center;
    justify-content: center;

    font-size: 20px;
    cursor: pointer;
    /* background-color: var(--transparentBackground); */
    background-color: black;
    border-radius: 1rem;
    color: white;
    border: none;
}

